@charset "UTF-8";
table {
  empty-cells: show;
  width: 100%;
  text-align: center; }

thead {
  background-color: #e0e0e0;
  vertical-align: bottom; }
  thead th {
    padding: 10px 20px; }

.table-row td {
  padding: 6px 0; }

.table-row {
  padding: 10px 0;
  color: #888; }

.table-row:nth-child(2n) {
  background: #f3f3f3; }

html {
  background-color: #FCFBF9; }

.admin td input[type="submit"], .admin td input[type="button"], .admin td input[type="reset"] {
  margin: 0 10px; }

.admin .edit-b {
  color: #FCFBF9 !important;
  background-color: #4EB5A2 !important;
  border-color: #4EB5A2 !important; }

.admin .itemlist li {
  margin-bottom: 25px;
  font-size: 1.4rem;
  border: 1px solid #cecece;
  padding: 10px 20px;
  border-radius: 4px; }
  .admin .itemlist li .brand {
    border: 1px solid #cecece;
    padding: 6px;
    border-radius: 4px;
    background-color: #eee;
    color: #95989A;
    font-size: 1rem; }

.admin .paginationwrap ul {
  text-align: center; }
  .admin .paginationwrap ul li {
    display: inline-block;
    width: 30px;
    border: 1px solid #cecece;
    border-radius: 4px;
    margin-bottom: 12px; }
    .admin .paginationwrap ul li:first-child, .admin .paginationwrap ul li:last-child {
      width: 40px;
      border: none; }
    .admin .paginationwrap ul li.active {
      background-color: #4EB5A2;
      color: #fff; }

.admin .annotation {
  font-size: 1.1rem;
  padding-left: 10px;
  color: #ce7070; }
  .admin .annotation::before {
    content: "※"; }
